@import "styles/color";

.container-blogs {
  .header {
    display: flex;
    width: 100%;
    position: relative;

    h1 {
      width: 25%;
    }
    .lines {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      .line-green {
        width: 40%;
        border: 2px solid $green;
      }
      .line-gray {
        width: 60%;
        border: 2px solid $slate-gray;
      }
    }
  }
}
