@import "styles/color";

.card01 {
  width: 400px;
  height: 450px;
  overflow: hidden;
  border-radius: 2px;
  background-color: $outer-space;
  color: white;
  img {
    width: 100%;
    height: 250px;
    object-fit: cover;
  }
  .card01-container {
    width: 90%;
    margin-left: 5%;
    overflow: hidden;
    .card01-category {
      color: $red;
      font-weight: bold;
    }
    h3 {
      margin-top: 0;
    }
    p {
      color: $platinum;
    }
  }
}
